<template>
  <div class="person-page">
    <br>
    <button class="back" @click="goBack"></button>
    <br>
    <h1>{{message}}</h1>
    <div class="player-container">
      <video-player class="vjs-custom-skin" ref="videoPlayer" :playsinline="true" :options="playerOptions"></video-player>
      <!-- demo https://surmon-china.github.io/vue-video-player/   -->
      <!-- https://www.jianshu.com/p/532fc1d8c90c -->
    </div>

    <br>
    <!--  -->
    <CountDown :start='startTime' :deadline='deadlineTime'>{{deadlineTime}}-{{startTime}}</CountDown>
    <br>
  </div>
</template>

<script>
  // VideoPlayer main.js
  /* import Vue from 'vue'
  import VideoPlayer from 'vue-video-player'
  Vue.use(VideoPlayer) */
  //video
  import 'video.js/dist/video-js.css'
  import 'vue-video-player/src/custom-theme.css'

  //hls.js
  import 'videojs-contrib-hls.js/src/videojs.hlsjs'

  import CountDown from '../../components/CountDown.vue'

  export default {
    name: "Person",
    data () {
      return {
        message:"Welcome to Person Page",
        playerOptions: {
          playbackRates: [0.7, 1.0, 1.5, 2.0], //
          autoplay: false, //true,
          controls: true, //
          preload: 'auto', //
          muted: false, //
          loop: false, //
          language: 'zh-CN',
          aspectRatio: '16:9', //  - "16:9""4:3"
          fluid: true, // trueVideo.js player
          sources: [{
            type: 'video/mp4', //application/x-mpegURL
            // mp4 
            src: require('@/assets/videos/video.mp4'), // 
            //  http://vjs.zencdn.net/v/oceans.mp4
            // webm
            // src: "https://cdn.theguardian.tv/webM/2015/07/20/150716YesMen_synd_768k_vp8.webm"
          }],
          poster: "http://static.smartisanos.cn/pr/img/video/video_03_cc87ce5bdb.jpg", //
          width: document.documentElement.clientWidth,
          notSupportedMessage: '' //Video.js
        },
        deadlineTime: "2019/10/09", // 
        startTime: "2019/10/01"  // 
      }
    },
    components: {
      CountDown
    },
    methods: {
      goBack () {
        this.$router.go(-1);
      }
    },
    computed: {
    }
  }
</script>

<style lang="scss" scoped>
.person-page {
  width:100%;
}
</style>




